import React from 'react';
import { Row, Col, Statistic } from 'antd';
import { useSelector } from 'react-redux';

const Profile = () => {

  const { user } = useSelector(state => state.user);
  const { mainPosts } = useSelector(state => state.post);

  return (
    <>
      <div>
      <Row gutter={16} style={{margin: 50, backgroundColor: '#00001a', border: '2px soild black', padding: '170px'}}>
        <Col span={12}>
          <Statistic title="Post Count" value={mainPosts.length} valueStyle={{ color: 'white' }} style={{color: 'white'}} />
        </Col>
        <Col span={12}>
          <Statistic title="Your Class" value={'초심자'} />
        </Col>
      </Row>
      </div>
    </>
  )
}

export default Profile;